@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600;700;800&family=Inter:wght@300;400;500;600&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', sans-serif;
  background: #FFF8E7;
  color: #2D2D2D;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Nunito', sans-serif;
}

.App {
  min-height: 100vh;
}

/* Cooking Animation Styles */
@keyframes chop {
  0%, 100% { transform: translateY(0) rotate(0deg); }
  50% { transform: translateY(-10px) rotate(-5deg); }
}

@keyframes stir {
  0% { transform: rotate(0deg); }
  25% { transform: rotate(15deg); }
  50% { transform: rotate(0deg); }
  75% { transform: rotate(-15deg); }
  100% { transform: rotate(0deg); }
}

@keyframes pour {
  0% { transform: translateY(0) rotate(0deg); opacity: 1; }
  50% { transform: translateY(20px) rotate(15deg); opacity: 0.7; }
  100% { transform: translateY(40px) rotate(30deg); opacity: 0; }
}

@keyframes mix {
  0%, 100% { transform: scale(1) rotate(0deg); }
  50% { transform: scale(1.1) rotate(180deg); }
}

@keyframes cook {
  0%, 100% { transform: scale(1); opacity: 1; }
  50% { transform: scale(1.05); opacity: 0.9; }
}

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-20px); }
}

@keyframes pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

@keyframes slideIn {
  from { transform: translateX(-100%); opacity: 0; }
  to { transform: translateX(0); opacity: 1; }
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes heart {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.2); }
}

.animate-chop { animation: chop 1s ease-in-out infinite; }
.animate-stir { animation: stir 2s ease-in-out infinite; }
.animate-pour { animation: pour 1.5s ease-in-out infinite; }
.animate-mix { animation: mix 2s ease-in-out infinite; }
.animate-cook { animation: cook 1.5s ease-in-out infinite; }
.animate-float { animation: float 3s ease-in-out infinite; }
.animate-pulse { animation: pulse 2s ease-in-out infinite; }
.animate-slideIn { animation: slideIn 0.5s ease-out; }
.animate-fadeIn { animation: fadeIn 0.6s ease-out; }
.animate-heart { animation: heart 0.6s ease-in-out; }

/* Gradient backgrounds */
.gradient-peach {
  background: linear-gradient(135deg, #FFE5D9 0%, #FFB8B8 100%);
}

.gradient-mint {
  background: linear-gradient(135deg, #B8E6D5 0%, #8DD4C3 100%);
}

.gradient-lavender {
  background: linear-gradient(135deg, #E0BBE4 0%, #D4A5D8 100%);
}

/* Button styles */
.btn-primary {
  background: linear-gradient(135deg, #FFB8B8 0%, #FF9999 100%);
  color: white;
  padding: 12px 32px;
  border-radius: 50px;
  border: none;
  font-weight: 600;
  cursor: pointer;
  box-shadow: 0 4px 15px rgba(255, 184, 184, 0.4);
  transition: transform 0.2s, box-shadow 0.2s;
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(255, 184, 184, 0.5);
}

.btn-secondary {
  background: white;
  color: #FFB8B8;
  padding: 12px 32px;
  border-radius: 50px;
  border: 2px solid #FFB8B8;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
}

.btn-secondary:hover {
  background: #FFB8B8;
  color: white;
}

/* Card styles */
.card-glass {
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(12px);
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.8);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

/* Text gradients */
.text-gradient {
  background: linear-gradient(135deg, #FFB8B8 0%, #E0BBE4 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}